class MapContainer extends Component {
    
    constructor(props) {
        super(props);
        this.onScriptLoad = this.onScriptLoad.bind(this);
    }

    state = {
        markers: [],
        infos: [],
        isLoaded: false
    }
    
    componentDidMount() {
        if (!window.google) {
            var script = this.buildMapTagScript();
            var after = document.getElementsByTagName('script')[0];
            after.parentNode.insertBefore(script, after);

            //Importante. So pode acessar o maps apos o load
            script.addEventListener('load', e => {
                this.onScriptLoad();
            })
        } else {
            this.onScriptLoad();
        }

        this.props.markers.map((marker) => {
            return this.fetchData(marker);
        });

        this.setState({isLoaded: true})
    }

    fetchData(marker){
        var location = encodeURI(marker.name);

        // console.log(`https://api.foursquare.com/v2/venues/search?intent=match&ll=${marker.position.lat},${marker.position.lng}&query=${location}&client_id=${foursquare.client_id}&client_secret=${foursquare.client_secret}&v=20190223`);

        fetch(`https://api.foursquare.com/v2/venues/search?intent=match&ll=${marker.position.lat},${marker.position.lng}&query=${location}&client_id=${foursquare.client_id}&client_secret=${foursquare.client_secret}&v=20190223`, {
            // mode: 'no-cors' // 'cors' by default
            method: "GET"
        })
        .then(response => response.json())
        .then(
            data => !data.response.venues[0] ? [] : this.fetchPlace(marker.name, data.response.venues[0])
        )
        .catch (error => {
            console.log('Ocorreu um erro ao tentar obter as informações dos marcadores. ' + error.message);
        });
     
    }
    
    fetchPlace (markerName, place) {
        /* devido a limitacao de requests da api de detalhes do foursquare, tive que manter os dados retornados da api hardcoded. */

        /*
            Tratamento de dado hardcode, deixado com um dos itens sem dados para simular quando dado nao foi encontrado
        */
        
        details.map(detail => {
            if(detail.value && detail.key === markerName){
                this.setState({
                    infos: [...this.state.infos, {
                        key: detail.key,
                        value: detail.value
                    }]
                })
            }
            return;
        });
        
        /* Tratamento correto com a chamada da api de detalhe do foursquare */

        // fetch(`https://api.foursquare.com/v2/venues/${place.id}?client_id=${foursquare.client_id}&client_secret=${foursquare.client_secret}&v=20190223`, {
        //     // mode: 'no-cors' // 'cors' by default
        //     method: "GET"
        // })
        // .then(response => response.json())
        // .then(
        //     data => data.response.venue
        // ).then (
        //     info => {
        //         console.log(info)
        //         this.setState({
        //             infos: [...this.state.infos, {
        //                 key: markerName,
        //                 value: info
        //             }]
        //         })
        //     }
        // ).catch (error => {
        //     console.log("Ocorreu um erro ao tentar obter detalhes dos marcadores. " + error.message);
        // });
    }

    onScriptLoad() {
        const map = new window.google.maps.Map(
            document.getElementById("map"), 
            {
                zoom: 10, 
                center: {lat: -23.5505199, lng: -46.6333094 }
            }
        );
        this.setState({ map: map });
    }

    buildMapTagScript() {
        var script = document.createElement('script');
        script.type = 'text/javascript';
        script.src = `https://maps.google.com/maps/api/js?key=AIzaSyAyqlRkzuQkEOFiSYkn198oWO5zwAwKWP0`;
        script.async = true;
        script.defer = true;

        return script;
    }

    render(){
        if(!this.state.isLoaded) return <div>Loading...</div>

        return (
            
            <Map id="map" 
                className={this.props.onCollapse ? "" : "sidebar-size"}
                map={this.state.map} 
                markers={this.props.markers} 
                infos={this.state.infos} 
                addGoogleMarker={this.props.addGoogleMarker}
                showMarker={this.props.showMarker}
                isShowInfoWindow={this.props.isShowInfoWindow}
                currentMarker={this.props.currentMarker}
                lastInfoWindow={this.props.lastInfoWindow}
                prevInfoWindow={this.props.prevInfoWindow}
                onInfoWindowClose={this.props.onInfoWindowClose}
                closeAllMarkers={this.props.closeAllMarkers} >
            </Map>
        )
    }
}

